.wrap {
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;

}

.nav-main {
    background: #222;
    color: #fafafa;
    position: fixed;
    top: 0;
    height: 50px;
    -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
            box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
    width: 100%;
    z-index: 100
}

.nav-main:after {
    content: "";
    display: table;
    clear: both
}

@media (max-width: 960px) {
    .nav-main {
        height: auto
    }
}

.nav-main a {
    color: #e9e9e9;
    text-decoration: none
}

.nav-main .nav-site {
    display: -ms-inline-flexbox;
    display: inline-flex;
    display: -ms-flexbox;
    flex: 1 1 0%;
    -ms-flex: 1;
    padding-left: 0px;
}

.nav-site a:focus {
    outline: none;
}

.nav-main .nav-site li {
    margin: 0
}

.nav-main .nav-site li > a {
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
    padding: 0 10px;
    line-height: 50px;
    display: inline-block;
    height: 50px;
    color: #ddd;
    white-space: nowrap
}

@media (max-width: 960px) {
    .nav-main .nav-site li > a {
        display: -ms-flexbox;
        display: flex;
        display: -ms-flex;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
        padding: 0 5px;
        line-height: inherit
    }
}

.nav-main .nav-site li > a:hover {
    color: #fff
}

.nav-main .nav-site li > a.active {
    position: relative;
    color: #fafafa;
    background: #333
}

.nav-main .nav-site li > a.active:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    border-bottom: 3px solid #cc7a6f
}

@media (max-width: 960px) {
    .nav-main .nav-site li > a.active {
        background: inherit
    }
}

@media (max-width: 960px) {
    .nav-main .nav-site .nav-site-item--hidden-sm {
        display: none
    }
}

@media (max-width: 960px) {
    .nav-main .nav-site .nav-site-search {
        position: absolute;
        z-index: 1;
        right: 0;
        top: 0;
        margin: 0 10px
    }

    .nav-main .nav-site .nav-site-search:focus {
        width: 170px
    }
}

.nav-main .nav-site-internal {
    margin: 0 0 0 20px;
}

@media (max-width: 960px) {
    .nav-main .nav-site-internal {
        margin: 0
    }
}

.nav-main .nav-site-external {
    float: right;
    margin: 0
}

@media (max-width: 960px) {
    .nav-main .nav-site-external {
        position: absolute;
        top: 0;
        right: 55px;
        float: none
    }
}

.nav-main .nav-home {
    color: white;
    font-size: 20px;
    line-height: 50px;
    height: 50px;
    display: inline-block
}

@media (max-width: 960px) {
    .nav-main .nav-home {
        font-size: 20px
    }
}

.nav-main .nav-logo {
    vertical-align: middle;
    display: inline-block
}

/*FIXME: get rid of subzero margin*/
.nav-logo {
    margin-left: -10px;
    margin-right: 5px;
}

@media (max-width: 960px) {
    .nav-main .nav-logo {
        height: 30px;
        width: auto
    }
}

.nav-main ul {
    display: inline-block;
    vertical-align: top
}

.nav-main li {
    display: inline
}

.nav-lists {
    display: inline-block;
    vertical-align: top;
    width: calc(100% - 120px)
}

@media (max-width: 960px) {
    .nav-lists {
        display: -ms-flexbox;
        display: flex;
        display: -ms-flex;
        width: calc(100% + 20px);
        margin: 0 -10px;
        font-size: 0.9em
    }

    .nav-lists li {
        display: -ms-flexbox;
        display: flex;
        display: -ms-flex;
        -ms-flex: 1 0 auto;
        -ms-flex-align: center;
            align-items: center
    }

    .nav-lists li a {
        -ms-flex: 1;
        text-align: center;
        line-height: 1.2
    }
}


