html {
    height: 100%;
}


body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    height: 100%;
    width: 100%;
}

.container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    padding-top: 50px;
    -ms-flex-align: start;
        align-items: flex-start;
    height: calc(100vh - 50px);
    -ms-flex: 1 1 0%;
        flex: 1 1 0%;
}

.right {
    overflow-y: scroll;
}

.left, .right {
    height: calc(100vh - 50px);
    -ms-flex: 1 1 0%;
        flex: 1 1 0%;
    -ms-flex-preferred-size: 50%;
        flex-basis: 50%;
}

.left {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: calc(100vh - 50px);
}


